import Navbar from './scenes/Navbar'
import DotGroup from "./scenes/DotGroup"
import { useEffect, useState } from "react";
import useMediaQuery from "./hooks/useMediaquery";
import { motion } from "framer-motion";
import Landing from './scenes/Landing';
import About from './scenes/About';
import Project from './scenes/Project';
import Skill from './scenes/Skill';
import ContactPage from './scenes/ContactPage';
import Github from './scenes/Github';
 

function App() {
 const [isTopofPage,setTopofPage]=useState(true)
  const[selectedPage,setSelectedPage]=useState("home")
  const isAboutMediumScreen =useMediaQuery("(min-width:1060px)")
  const [loading,setloding]=useState(true)

  setTimeout(() => {
    setloding(false)
  }, 2500);
 
useEffect(()=>{
  const handleScroll=()=>{
    if(window.scrollY===0) setTopofPage(true);
    if(window.scrollY!==0) setTopofPage(false);
  }
  window.addEventListener("scroll",handleScroll);
  return ()=>window.removeEventListener("scroll",handleScroll)

},[])

 if(loading){
   return <div className='h-full w-full bg-loadingbg'>
     
         <img 
                  alt="profil"
                 className='m-auto justify-center w-auto h-[50%]'
                  src="https://cdn.dribbble.com/users/108183/screenshots/14420202/media/0398828bd84d67fad129e64e8a79f77c.gif"
                />

   </div>
 }

  return (
    <div className="app " >
      <Navbar 
      isTopofPage={isTopofPage}
      selectedPage={selectedPage }
      setSelectedPage={setSelectedPage} 
      />
     <div       className="w-5/6 mx-auto md:h-full ">
      {isAboutMediumScreen && (
          <DotGroup
           
          />
        )}
         <motion.div
          margin="0 0 -200px 0"
          amount="all"
          onViewportEnter={() => setSelectedPage("home")}
        >
          <Landing setSelectedPage={setSelectedPage} />
        </motion.div>
         <motion.div
          margin="0 0 -200px 0"
          amount="all"
          onViewportEnter={() => setSelectedPage("about")}
        >
           <motion.h1
             className=" text-black text-center"
             initial="hidden"
             whileInView="visible"
             viewport={{once:true,amount:0.5}}
             transition={{duration:0.5}}
             variants={{
                 hidden:{opacity:0,x:-50},
                 visible:{opacity:1,x:0}}}
           > About Me</motion.h1>
           <About setSelectedPage={setSelectedPage} /> 
       </motion.div>  
       <motion.div
          margin="0 0 -200px 0"
          amount="all"
          onViewportEnter={() => setSelectedPage("about")}
        >
           <Project/>
          
       </motion.div>
       <motion.div
          margin="0 0 -200px 0"
          amount="all"
          onViewportEnter={() => setSelectedPage("about")}
        >
           <Skill/>
           {/* <About setSelectedPage={setSelectedPage} />  */}
       </motion.div>  
       <motion.div
          margin="0 0 -200px 0"
          amount="all"
          onViewportEnter={() => setSelectedPage("about")}
        >
           <Github/>
           
       </motion.div>    
       <motion.div
          margin="0 0 -200px 0"
          amount="all"
          onViewportEnter={() => setSelectedPage("about")}
        >
           <ContactPage/>
           
       </motion.div>  

    </div>  
      {/* <div className=' h-6  '>

      </div> */}
       
    </div>
  );
}

export default App;
