import React from 'react';
import { motion } from 'framer-motion';
import pharmeasy from '../assets/pharmeasy.png'
import Indian_Express from '../assets/IndianExpress.png'
import Rentmojo from '../assets/Rentmojo.png'
import cricinfo from '../assets/cricinfo.png'


const container={
    hidden:{},
    visible:{
        transition:{staggerChildren:0.2}
    }
}

const projectVariant={
    hidden:{opacity:0,scale:0.8},
    visible:{opacity:1,scale:1}
}

const data=[

    {title:"Pharmeasy-website-clone",
    description:"pharmacy one of India's top online PharmEasy and medical care platform This Project was individually built by me and it was completed within just 5 days" ,
    image:pharmeasy,
    github:"https://github.com/jithstephen13/PharmEasy-macabre-sun-7300",
    website:"https://spiffy-parfait-1c8f71.netlify.app/",
    id:1
},
{title:"Indian Express-website-clone",
description:"The Indian Express is an English-language Indian daily newspaper website This Project was collaboratively built by Four members and it was completed within just 5 days" ,
image:Indian_Express,
github:"https://github.com/deepakkumarfathkar/Indian_Express_Clone",
website:"https://admirable-fairy-1b98c0.netlify.app/",
id:2
},
{title:"Temp_Ray website",
description:"It is the clone of Rentmojo.com this website provides furniture and appliance for rentThis Project was individually built by me and it was completed within just 5 days" ,
image:Rentmojo,
github:"https://github.com/jithstephen13/Temp_Ray-app/tree/main/temp_app",
website:"https://tem-ray-app.netlify.app/",
id:3
},
{title:"ESPN cricinfo website-clone",
description:"ESPN cricinfo is a sports news website exclusively for the game of cricket. The site features news, articles, live coverage of cricket matches status available in this web site" ,
image:cricinfo,
github:"https://github.com/jithstephen13/ESPNcricinfo-gamy-lace-3375-",
website:"https://deluxe-buttercream-9cdfdb.netlify.app/",
id:4
}
]


const ProjectItem=({title,description,image, github,website})=>{
    const overlaystypes=` absolute h-full w-full opacity-0 
       hover:opacity-90 transition  duration-500 bg-white z-30 flex p-2
       flex-col justify-center items-center text-center  text-black rounded-lg`

    return(
        <motion.div id='project' variants={projectVariant} className="relative mb-8 ">
            <div className={overlaystypes}>
                <p className=' text-2xl font-playfair p-6'> {title}</p>
                 <p>{description}</p>

                 <div className='flex justify-around w-full p-10'> 
                 <button className='bg-black rounded-tl-lg w-24  rounded-tr-lg rounded-b-xl p-2 text-white'> <a href={website}>website</a></button> 
                 <button  className='bg-black rounded-tl-lg w-24 rounded-tr-lg rounded-b-xl p-2 text-white'><a href={github}>code</a> </button></div>
            </div>
            <img className='rounded-lg' src={image}    alt={Math.random()} />

        </motion.div>
    )
}



const Project = () => {
    return (
        <section  className='pt-42 pb-42 '>
            <motion.div
              className='text-center md:w-2/4 mx-auto  mt-28'
              initial="hidden"
              whileInView="visible"
              viewport={{once:true, amount:0.5}}
              transition={{duration:0.5}}
              variants={{
                hidden:{opacity:0,x:-50},
                visible:{opacity:1,x:0}
              }}
            >
                 <h1 className="ssm:text-xl text-2xl font-small title-font mb-4 text-black">
                MY PROJECT
              </h1>
              


            </motion.div>

      {/* project       */}

            <div className='flex justify-center'>
                  
            <motion.div
              className='sm:grid sm:grid-cols-2 gap-32 rounded-lg    '
              initial="hidden"
              whileInView="visible"
              viewport={{once:true, amount:0.5}}
              transition={{duration:0.5}}
              variants={{
                hidden:{opacity:0,x:-50},
                visible:{opacity:1,x:0}
              }}
            >
             {
                data.map((item)=> <ProjectItem key={item.id} {...item} />)
             }   

            </motion.div>


            </div>


        </section>
    );
};

export default Project;