import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';


const ContactPage = () => {


  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_hutwrhn', 'template_oph5o6g', form.current, 'pc7y9Dv2GAuWDsXZH')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });

     form.current.reset()
  };


    return (
       
        <div id='contact'>
        <section className="text-gray-700 body-font relative">
          <div className="container px-5 py-24 mx-auto">
            <div className="flex flex-col text-center w-full mb-12">
              <h1 className="sm:text-3xl text-2xl font-medium title-font mb-4 text-black">
                Contact Me
              </h1>
              <p className="lg:w-2/3 mx-auto leading-relaxed text-base  text-black ">
               
              </p>
            </div>
            <div className="lg:w-1/2 md:w-2/3 mx-auto">
              <div className="flex flex-wrap -m-2">
                <form  ref={form} onSubmit={sendEmail}
                 className="flex flex-wrap -m-2"
                >
                <div className="p-2 w-1/2">
                  <div className="relative">
                    <label  className="leading-7 text-sm  text-black">
                      Name
                    </label>
                    <input
                      type="text"
                      id="name"
                      name="user_name"
                      className="w-full bg-gray-100 rounded border border-gray-300
                       focus:border-indigo-500 text-base outline-none  text-black py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                    />
                  </div>
                </div>
                <div className="p-2 w-1/2">
                  <div className="relative">
                    <label
                     
                      className="leading-7 text-sm text-black"
                    >
                      Email
                    </label>
                    <input
                      type="email"
                      id="email"
                      name="user_email"
                      className="w-full bg-gray-100 rounded border border-gray-300 focus:border-indigo-500 text-base outline-none text-black py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                    />
                  </div>
                </div>
                <div className="p-2 w-full">
                  <div className="relative">
                    <label
                      
                      className="leading-7 text-sm text-black "
                    >
                      Message
                    </label>
                    <textarea
                      id="message"
                      name="message"
                      className="w-full bg-gray-100 rounded border border-black focus:border-indigo-500 h-32 text-base outline-none text-black py-1 px-3 resize-none leading-6 transition-colors duration-200 ease-in-out"
                    ></textarea>
                  </div>
                </div>
                <div className="p-2 w-full">
                  <button
                   type="submit" value="Send" 
                   className="flex mx-auto text-white bg-blue border-0 py-2 px-8 focus:outline-none
                    hover:text-black rounded text-lg">
                  Send Message
                  </button>
                </div>
                </form>
                <div className="p-2 w-full pt-8 mt-8 border-t border-black text-center">
                  <a className="text-black"> jithstephen@email.com </a>
                  <p className="leading-normal my-5 text-black">
                  
                    <br />
                    wayanad ,Kerala
                  </p>
                  <span className="inline-flex">
                    <a className="text-black">
                      <svg
                        fill="currentColor"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        className="w-5 h-5"
                        viewBox="0 0 24 24"
                      >
                        <path d="M18 2h-3a5 5 0 00-5 5v3H7v4h3v8h4v-8h3l1-4h-4V7a1 1 0 011-1h3z"></path>
                      </svg>
                    </a>
                   
                    <a className="ml-4 text-black">
                      <svg
                        fill="none"
                        stroke="currentColor"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        className="w-5 h-5"
                        viewBox="0 0 24 24"
                      >
                        <rect
                          width="20"
                          height="20"
                          x="2"
                          y="2"
                          rx="5"
                          ry="5"
                        ></rect>
                        <path d="M16 11.37A4 4 0 1112.63 8 4 4 0 0116 11.37zm1.5-4.87h.01"></path>
                      </svg>
                    </a>
                 
                  </span>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
        
    );
};

export default ContactPage;