import useMediaQuery from "../hooks/useMediaquery";
import {motion} from 'framer-motion'
import AnchorLink from "react-anchor-link-smooth-scroll";
import profile from '../assets/profile-image.JPG'

const About = ({setSelectedPage}) => {
    const isAboveMediumscreen =useMediaQuery("(min-width:1060px)")
   
    return (
        <section id="about" className="md:flex flex-col  md:justify-between md:items-center gap-16 md:h-fulln  py-10">
            
             <motion.div 
             className="flex justify-center"
                    initial="hidden"
                    whileInView="visible"
                    viewport={{once:true,amount:0.5}}
                    transition={{duration:0.5}}
                    variants={{
                        hidden:{opacity:0,x:-50},
                        visible:{opacity:1,x:0}}}> 
                        <img 
                  alt="profile"
                  className=" hover:filter w-72 w hover:saturate-50 transition duration-150 rounded-full "
                  src={profile}
                />
 </motion.div>
                  
                <motion.div 
                className="flex flex-col justify-center items-center"
                    initial="hidden"
                    whileInView="visible"
                    viewport={{once:true,amount:0.5}}
                    transition={{duration:0.5}}
                    variants={{
                        hidden:{opacity:0,x:-50},
                        visible:{opacity:1,x:0}}}>
                         <p className="mt-10 mb-7  text-center text-2xl  w-[60%] bg-clip-text text-transparent bg-gradient-to-r from-stone-700 to-violet-500">Goal-oriented Full Stack Web Developer from Masai School having 1200 hr Coding experience and looking to enhance my professional experience with an IT company specializing in web development. I am armed with a deep sense of responsibility and enthusiasm to give 110% for any endeavor.</p> 
                         <div className="mt-10 shadow-gray-500">
                          <AnchorLink className="bg-blue  rounded-2xl py-7 px-7 
                        hover:bg- hover:text-black transition duration-200 text-center "
                        onClick={()=>{setSelectedPage("contact")}}
                        href="#contact">
                            conntact me
                        </AnchorLink> </div>
                       
                    </motion.div>
                
  
               
    </section>
    );
};

export default About;