


import React, { useState } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import jithlogo from '../assets/jithlogo.png'

import useMediaQuery from '../hooks/useMediaquery';
import Resume from '../assets/Jith_ Stephen_Resume.pdf'


const Link=({page,selectedPage,setSelectedPage})=>{
    const lowerCasePage=page.toLowerCase();
    return (
        <AnchorLink className={`${selectedPage===lowerCasePage ? "text-yellow":""} hover: text-yellow transition duration-500 `}
        href={`#${lowerCasePage}`}
        >{page }</AnchorLink>
    )
}


const Navbar = ({isTopofPage,selectedPage,setSelectedPage}) => {
 const [menuTogled,setTogled]=useState(false) ;
 const isAboutSmallscreen=useMediaQuery("(min-width:768px")  
 const navbarBackground=isTopofPage? "" : "bg-gray"
 

 return (
        <nav  className={ `bg-black z-40 w-full fixed top-0 py-1 `}>
            <div className="flex items-center justify-around mx-auto  ">
            
             <img src={jithlogo} alt="logo"  className='h-14 p-0 hover:cursor-pointer' />
             {/* {desktop} */}

             {isAboutSmallscreen ? ( <div  className="flex justify-between gap-16 font-opensans text-sm font-semibold">
           <Link
           page="Home"
          
            />
             <Link
           page="About"
          
            />
             <Link
           page="Project"
           
            />
             <Link
           page="skills"
          
            />
             <Link
           page="Contact"
          
            />
             </div> ):(<button className="rounded-full bg-red p-2"
             onClick={()=>{setTogled(!menuTogled)}}
             >
                 <svg className="w-6 h-6" aria-hidden="true" fill='currentColor' viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z" clipRule="evenodd"></path></svg>
                </button>)}

               {/* mobile menu  popup */}

               {!isAboutSmallscreen && menuTogled &&(
                <div className='fixed right-0 bottom-0 h-full bg-blue w-[300px]'>
                    {/* close this icon */}
                    <div className='flex justify-end p-12' >
                        <button onClick={()=>setTogled(!menuTogled)}>
                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                         <path d="M19 6.41L17.59 5L12 10.59L6.41 5L5 6.41L10.59 12L5 17.59L6.41 19L12 13.41L17.59 19L19 17.59L13.41 12L19 6.41Z" fill="white" fill-opacity="0.54"/></svg>  
                        </button>
                    </div>

                    {/* menu items */}
                    <div className="flex flex-col gap-10 ml-[33%] text-2xl text text-deep-blue  bg-blue ">   
                          
                    <Link
           page="Home"
           selectedPage={selectedPage}
           setSlectedPage={setSelectedPage}
            />
            <Link
           page="About"
           selectedPage={selectedPage}
           setSlectedPage={setSelectedPage}
            />
             <Link
           page="Skils"
           selectedPage={selectedPage}
           setSlectedPage={setSelectedPage}
            />
             <Link
           page="Project"
           selectedPage={selectedPage}
           setSlectedPage={setSelectedPage}
            />
             <Link
           page="Testumonials"
           selectedPage={selectedPage}
           setSlectedPage={setSelectedPage}
            />
             <Link
           page="contact"
           selectedPage={selectedPage}
           setSlectedPage={setSelectedPage}
            />
            <a href={Resume} download>
             <button className="bg-white  rounded-sm   rounded-full-50% w-40
                            hover:bg-white hover:text-black transition duration-200   font-playfair"
                            >
                                Download CV
                      </button></a>
                          
                     </div>
                   
                        


                </div>
               )}  
                   {isAboutSmallscreen&& <a href={Resume} download>
                    <button className="bg-blue  rounded-sm py-2 px-2 rounded-full-50%
                            hover:bg-white hover:text-black transition duration-200   font-playfair"
                           
                            >
                                Download CV
                      </button></a>}

            </div>
            
        </nav>
    );
};

export default Navbar;